  text-align: center;
  width: 100%;
  display: flex;
  justify-content: center;
  font-weight: 600;
  font-size: calc(15px + 1 * (100vw - 320px) / 1600);
  line-height: 1.2;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  display: -webkit-box;
  overflow: hidden;
}
.progtrckr li:before {
  position: relative;
  top: -2px;
  float: left;
  left: 50%;
  line-height: 1;
  transform: translate(-50%, -50%);
}
